import React, {Component} from 'react'
import {List, InputItem, WingBlank, WhiteSpace, Button, Radio,Flex,NavBar,Icon} from 'antd-mobile'
import {connect} from 'react-redux'
import {Redirect} from 'react-router-dom'
import {ElementBlank} from '../../component'

import './set-password.css'


export default class Register extends Component {

    constructor(props) {
        super(props)
    }

    goBack = ()=> {
        this.props.history.goBack()
    }

    goHome = ()=> {
        this.props.history.push('/home')
    }

    render() {
        return(
            <div>
                <WingBlank>
                    <NavBar mode="light" icon={<Icon type="left" size="lg"/>} onLeftClick={this.goBack} ></NavBar>
                    <ElementBlank height={40} />
                    <div className="setpwd-tips"></div>
                    <ElementBlank height={10} />
                    <div className="setpwd-tips">+86 13485244567</div>
                    <ElementBlank height={30} />
                    <List>
                        <InputItem placeholder="" type="password">
                            <i className="iconfont icon-password"></i>
                        </InputItem>
                        <ElementBlank height={10} />
                        <InputItem placeholder="" type="password">
                            <i className="iconfont icon-password"></i>
                        </InputItem>
                    </List>
                    <ElementBlank height={50}/>
                    <Button type="primary" onClick={this.goHome}></Button>
                    <ElementBlank height={20} />
                </WingBlank>
            </div>
        )
    }

}


